.home-container {
    background: $white;
    @include media-breakpoint-down(xl) {
        padding: 0px 64px;
    }
    @include media-breakpoint-down(lg) {
        padding: 0px 40px;
    }
    @include media-breakpoint-down(md) {
        padding: 0px 20px;
    }
    @include media-breakpoint-down(sm) {
        padding: 0px 10px;
    }

    .section-intro {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        @include media-breakpoint-down(xl) {
            height: 900px;
        }
        @include media-breakpoint-down(lg) {
            height: 600px;
        }
        @include media-breakpoint-down(md) {
            height:500px;
        }
        @include media-breakpoint-down(sm) {
            height: 300px;
        }
        .intro-content {
            width: 45%;
            @include media-breakpoint-down(lg) {
                width: 100%;
            }
            .intro-title {
                color: $primary;
                font-size: $h1-font-size * 2;
                @include media-breakpoint-down(md) {
                    font-size: $h1-font-size;
                    padding-top: 60px;
                }
                font-weight: $font-weight-bold;
            }
            .intro-type {
                color: $black;
                font-size: $h1-font-size * 2;
                @include media-breakpoint-down(md) {
                    font-size: $h1-font-size;
                }
                font-weight: $font-weight-bold;
            }
            .intro-desc {
                color: $gray-600;
                width: 100%;
                font-size: $h5-font-size;
                @include media-breakpoint-down(md) {
                    font-size: $h6-font-size;
                }
                font-weight: $font-weight-light;
            }

            .intro-connect-btnGroup {
                display: flex;
                flex-direction: column;
                
                @include media-breakpoint-down(md) {
                    flex-direction: row;
                }
                @include media-breakpoint-down(sm) {
                    flex-direction: column;
                    //align-items: center;
                    //justify-content: center;
                }
                .btn {
                    padding-left: 20px;
                    @include media-breakpoint-down(sm) {
                        padding-left: 10px;
                    }
                    border-radius: 10px;
                    width: 50%;
                    @include media-breakpoint-down(md) {
                        width: 100%;
                    }
                    height: auto;
                    text-align: left;
                }

                .btn-outline-secondary {
                    color: $secondary;
                }

                .connect-img {
                    width: 40px;
                    height: 40px;
                }
                .intro-btn-wallet {
                    @include media-breakpoint-down(md) {
                        padding-right: 20px;
                    }
                    @include media-breakpoint-down(sm) {
                        padding-right: 0px;
                        width: 60%;
                    }
                    @include media-breakpoint-down(xs) {
                        width: 100%;
                    }
                    > .btn-primary {
                        color: $white;
                    }
                }
                .intro-btn-metamask {
                    > .btn-primary {
                        background-color: $white;
                    }

                    .btn-primary:hover {
                        color: $white;
                        background: $secondary;
                    }

                    @include media-breakpoint-down(sm) {
                        width: 60%;
                    }
                    @include media-breakpoint-down(xs) {
                        width: 100%;
                    }
                }
            }
        }
        .intro-image {
            width: 75%;
            height: auto;
            position: absolute;
            top: 0;
            right: -64px;

            @include media-breakpoint-down('lg') {
                display: none;
            }
        }
        .intro-ticket {
            position: absolute;
            width: 30%;
            height: auto;
            top: 10%;
            right: 5%;
            .upload-image {
                display: none;
                position: absolute;
                left: 20px;
                bottom: 130px;
                font-size: 24px;
                color: $primary;
            }
            &:hover {
                .upload-image {
                    display: block;
                }
            }
            .ticket-img {
                width: 100%;
                height: auto;
            }

            @include media-breakpoint-down(lg) {
                display: none;
            }

            .ticket-content {
                background-color: $white;
                color: $black;
                .edit-content {
                    position: absolute;
                    bottom: 20px;
                    right: 20px;
                    font-size: 24px;
                    color: $secondary;
                    cursor: pointer;
                    display: none;
                }
                &:hover {
                    .edit-content {
                        display: block;
                    }
                }
                .title {
                    padding-top: 20px;
                    padding-bottom: 20px;
                    font-size: 32px;
                    font-weight: $font-weight-bold;
                }
                .price {
                    color: $primary;
                    padding-bottom: 20px;
                    font-size: 20px;
                    font-weight: $font-weight-bold;
                }
            }
        }
    }

    .section {
        padding-top: 100px;
        position: relative;
        z-index: 2;
        &.top-sellers {
            @media screen and (max-width: 1700px) {
                margin-top: -100px;
            }
            @media screen and (max-width: 1600px) {
                margin-top: -150px;
            }
            @media screen and (max-width: 1500px) {
                margin-top: -200px;
            }
            @media screen and (max-width: 1400px) {
                margin-top: -250px;
            }
            @media screen and (max-width: 1300px) {
                margin-top: -300px;
            }
            @media screen and (max-width: 1200px) {
                margin-top: -100px;
                padding-top: 0;
            }
            @media screen and (max-width: 780px) {
                margin-top: 30px;
            }
        }
        @include media-breakpoint-down('lg') {
            padding-top: 40px;
        }
        color: $black;

        .section-title {
            padding-top: 40px;
            padding-bottom: 40px;
            text-align: left;
        }

        .justify-center {
            justify-content: left;
            @include media-breakpoint-down(sm) {
                justify-content: center;
            }
        }

        .btn-type {
            border: 1px solid $primary;
            background-color: transparent;
            color: $primary;
            padding-right: 40px;
            padding-left: 40px;
        }
        .btn-type:hover {
            background-color: $primary;
            color: $white;
        }

        .seller-segment {
            display: flex;
            flex-direction: row;
            align-items: center;
            cursor: pointer;
            text-align: left;
            .seg-img {
                width: 50px;
                height: 50px;
                border: 2px solid $purple;
                border-radius: 100%;
                margin-right: 12px;
            }
            .seg-name {
                font-weight: $font-weight-bold;
                font-size: 16px;
                color: $black;
            }
            .seg-type {
                font-size: 16px;
                font-weight: $font-weight-bold;
                color: $gray-600;
            }
            .seg-price {
                font-size: 14px;
                font-weight: $font-weight-bold;
                color: $primary;
            }
            .seg-price-eth {
                font-size: 14px;
                font-weight: $font-weight-bold;
                color: $blue;
            }
        }
    }

    .modal-title {
        color: $black;
        font-size: 24px;
        font-weight: $font-weight-bold;
    }
    .header-search-form {
        box-sizing: border-box;
        border-radius: 5px;
        padding: 8px 16px;
        margin-right: 20px;
        border: 1px solid grey;
        .form-control {
          width: 200px;
          background: transparent;
          border: none;
          outline: none;
          height: 30px;
          padding: 0 0.75rem;
          box-shadow: inset 0 0px 0 #ddd !important;
          &:focus {
            background: transparent;
          }
        }
    }
}
