body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  overflow-x: hidden;
  line-height: 1;
  padding: 0 !important;
  &::-webkit-scrollbar {
    width: 5px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: transparent;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: #9e9fa0;
    border-radius: 3px;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: #9e9fa0;
  }
  .bn-onboard-custom {
    z-index: 1000;
  }
}

.pointer-cursor {
  cursor: pointer;
}

.faint-color {
  background: linear-gradient(90deg, $primary, $purple, $warning);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
  background-color: $black;
}

.no-data {
  margin-left: auto;
  margin-right: auto;
  max-width: 300px;
  text-align: center;
}

.bid-history-item {
  align-items: center;
  .time-ago {
    font-size: 14px;
    color: rgba($black, 0.7);
  }
}
.btn {
  &.full-width {
    width: 100%;
  }
}

.loading-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba($dark, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100000;
}

a {
  &:hover {
    text-decoration: none;
  }
}

.carousel {
  margin-bottom: 40px;
  .carousel-indicators {
    bottom: -40px;
    li {
      background-color: grey;
      opacity: 1;
      border-top: 0px solid transparent;
      border-bottom: 0px solid transparent;
      width: 50px;
      height: 6px;
      border-radius: 6px;
      margin-right: 5px;
      &.active {
        background-color: black;
      }
    }
  }
  .carousel-control-prev, .carousel-control-next {
    width: 5%;
    opacity: 1;
    @media screen and (max-width: 768px) {
      width: 10%;
    }
    span {
      width: 40px;
      height: 40px;
      border-radius: 40px;
      background-size: 50%;
      background-color: $gray-900;
    }
  }
}